<template>
    <el-row>
        <el-col :span="6">
            <div class="grid-content bg-purple-dark">
                <el-input placeholder="请输入过滤条件" v-model="searchKey"></el-input>
            </div>
        </el-col>
        <el-col :span="18">
            <div class="grid-content bg-purple-dark" style="text-align: right">
                <el-button type="primary" @click="handleEdit('','')">添加用户</el-button>
            </div>
        </el-col>
        <el-col :span="24">
            <div id="gather-data-form" class="grid-content bg-purple-dark">
                <el-table :data="filteredList" width="100%" align="right" :stripe="true" header-align="right" highlight-current-row >
                    <el-table-column type="index" width="50"></el-table-column>
                    <el-table-column prop="name" label="用户名"></el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <el-button
                                    size="mini"
                                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                            <el-button
                                    size="mini"
                                    type="danger"
                                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </el-col>
        <el-col :span="24">
            <div class="grid-content bg-purple-dark">
                <el-dialog
                        title="用户信息"
                        :visible.sync="dialogVisible"
                        width="60%"
                        :before-close="handleClose">
                    <span>
                        <el-form ref="form" :model="form" label-width="100px">
                          <el-form-item label="用户名">
                            <el-input v-model="form.name" ></el-input>
                          </el-form-item>
                          <el-form-item label="密码">
                            <el-input v-model="form.pwd"></el-input>
                          </el-form-item>
                        </el-form>
                    </span>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="dialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="editData">更 新</el-button>
                    </span>
                </el-dialog>
            </div>
        </el-col>
    </el-row>
</template>

<script>
    import Service from './service'
    export default {
      name: 'appManage',
      data () {
        return {
          searchKey: '',
          dialogVisible: false,
          dataList: [],
          form: {
            name: '',
            pwd: ''
          }
        }
      },
      methods: {
        handleClose () {
          this.dialogVisible = false
        },
        handleEdit(index, row) {
          this.dialogVisible = true
          this.form.name = row.name
          this.form.pwd = row.obj
          console.log(index, row)
        },
        handleDelete(index, row) {
          console.log(index, row)
        },
        editData() {
          Service.updateMeteData({username: this.form.name, password: this.form.pwd}).then(res => {
            this.dialogVisible = false
            this.userList()
          })
        },
        userList() {
          Service.getUserList().then((data = {}) => {
            this.dataList = Object.keys(data.data).reduce((res, key, index) => {
              res.push({name: key, obj: data.data[key], index: index})
              return res
            }, [])
          }).catch(error => {
            console.log(error)
            this.$message.error('请求失败，请联系程序猿改BUG!')
          })
        }
      },
      mounted () {
        this.userList()
      },
      computed: {
        'filteredList' () {
          return this.dataList.filter(item => item.name.includes(this.searchKey)) || []
        }
      }
    }
</script>

<style scoped>

</style>


