import axios from 'axios'
import Cookie from 'js-cookie/src/js.cookie'

export default {
  getUserList (url = 'profile/getUserList') {
    axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
    axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
    return axios.get(url, {params: {token: Cookie.get('token')}})
  },
  updateMeteData (param, url = 'profile/add') {
    axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
    axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
    param.token = Cookie.get('token')
    return axios.get(url, {params: param})
  }
}



